<script>
export default {
  data() {
    return {
      QQ:"tencent://Message/?Uin=1532092521&websiteName=www.oicqzone.com&Menu=yes",
      music:1
    };
  },
  methods:{

    //加QQ好友
    jqq(){
      location.href = this.QQ
    },

    //微信二维码  鼠标移入显示
    xt(){
      let a = document.querySelector(".xsyc")
      a.style.display = 'block'
    },
    // 微信二维码  鼠标移出隐藏
    hideQrCode() {
    let qrCode = document.querySelector(".xsyc");
    qrCode.style.display = 'none';
  },
  play(){
    this.music++
    if(this.music % 2 === 0){
      this.$refs.yinyue.play()
    }else{
      this.$refs.yinyue.pause();
    }
    
  }
  }
};
</script>

